.gallery {
background: linear-gradient(
    to top,
    var(--color-design-white) 60%,
    var(--bg-section) 60%
);
}

.grid {
    display: block;
    margin: 0 auto;
    @media screen and (max-width: 340px) {
        display: none;
    }
}

.grid:after {
content: '';
    display: block;
    clear: both;
}

.grid__item {
    overflow: hidden;
    margin-bottom: 6px;
    width: 120px;
    height: 120px;
    float: left;
    border-radius: 16px;
    z-index: 0;
    transition: all ease 500ms;
}

.grid__image {
}

.grid__item--width2 {
    width: 246px;
}
.grid__item--width3 {
    width: 432px;
}

.grid__item--height2 {
    height: 246px;
}
.grid__item--height3 {
    height: 432px;
}

.grid__item--gigante {
    transform: scale(1.3);
    z-index: 2;
    @media screen and (max-width: 340px) {
        display: none;
    }
}

.grid__item:hover {
    background: #a2c;
    border-color: white;
    cursor: pointer;
}
.stamp {
    position: absolute;
}
.stamp1 {
    left: 0;
    top: 0;
    width: 20%;
    height: 146px;
}
.stamp2 {
    right: 0;
    top: 0;
    width: 20%;
    height: 146px;
}
.stamp3 {
    right: 10%;
    top: 20px;
    width: 30%;
    height: 30px;
}
.stamp4 {
    left: 10%;
    top: 20px;
    width: 30%;
    height: 30px;
}