.customer-reviews {
    padding-top: 80px;
    padding-bottom: 80px;
    & .swiper-button-next,
    & .swiper-button-prev {
        display: none;
    }
}
.customer-reviews__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--color-design-black);
}
.customer-reviews__image {
    margin-bottom: 30px;
}
.customer-reviews__wrapper {
    margin-bottom: 30px;
    padding: 43px 50px 20px 50px;
    background-color: var(--bg-quote);
    min-width: 280px;
    z-index: -1;
    @media screen and (min-width: 768px) {
        margin-bottom: 25px;
    }
    @media screen and (min-width: 1280px) {
        margin-bottom: 15px;
    }
}
.customer-reviews__feedback {
        position: relative;
        font-weight: 500;
        font-size: 12px;
        line-height: calc(31 / 12);
        text-align: center;
        letter-spacing: 0.04em;
        color: black;
    
        background-image: linear-gradient(
        0deg,
        #e1e1e1 1.61%,
        transparent 1.61%,
        transparent 50%,
        #e1e1e1 50%,
        #e1e1e1 51.61%,
        transparent 51.61%,
        transparent 100%
        );
        background-size: 62px 62px;
    
        z-index: 1;
        @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: calc(31 / 14);
        }
        @media screen and (min-width: 1280px) {
        font-size: 16px;
        line-height: calc(31 / 16);
        }

        &::before {
        content: '';
        position: absolute;
        display: block;
        top: -14px;
        left: 10px;
        width: 54px;
        height: 40px;
    
        background-image: url('../images/customer-reviews/customers-reviews-brackets.svg');
        background-repeat: no-repeat;
        background-size: contain;
    
        z-index: -1;
    
        @media screen and (min-width: 480px) {
            left: -20px;
        }
        }
}
.customer-reviews__location {
    font-weight: 500;
    font-size: 14px;
    line-height: 186%;
    text-align: center;
    margin-bottom: 30px;
    color: black;
    &::before {
        content: '';
        display: block;
        margin: 0 auto;
        margin-bottom: 18px;
        text-align: center;
        width: 32px;
        height: 4px;
        background-image: url('../images/customer-reviews/customers-reviews-pagination-dots.svg');
        @media screen and (min-width: 768px) {
            margin-bottom: 15px;
        }
    }
    @media screen and (min-width: 768px) {
        font-size: 16px;
        margin-bottom: 35px;
    }
    @media screen and (min-width: 1280px) {
        font-size: 20px;
    }
}
.customer-reviews__dots {
    display: inline-block;
    margin: 0 6px;
    border-radius: 50%;
    width: 12px;
    height: 12px;
    background-color: #d9d9d9;
}
.customer-reviews__dots--active {
    background-image: url('../images/customer-reviews/customers-reviews-pagination-home.svg');
    background-repeat: no-repeat;
    background-size: contain;
    border-radius: 0;
    background-color: transparent;
    width: 13px;
    height: 13px;
}
.customer-reviews .customer-reviews__paginationbullets {
    bottom: 0px;
    left: 0;
    width: 100%;
}