.bubbles {
  width: 96%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
}

.bubbles span {
  opacity: .8;
  background-color: #ffa5ba;
  border-radius: 50%;
  position: absolute;
}

.bubbles span:nth-child(1) {
  width: 301px;
  height: 250px;
  animation: bubble 6s ease-in-out infinite;
  top: 19%;
  left: -1%;
}

.bubbles span:nth-child(2) {
  width: 150px;
  height: 130px;
  animation: bubble 4s ease-in-out infinite;
  top: 33%;
  left: 78%;
}

.bubbles span:nth-child(3) {
  width: 400px;
  height: 350px;
  animation: bubble 5s ease-in-out infinite;
  top: 35%;
  left: 45%;
}

.bubbles span:nth-child(4) {
  width: 80px;
  height: 70px;
  animation: bubble 3s ease-in-out infinite;
  top: 50%;
  left: 10%;
}

.bubbles span:nth-child(5) {
  width: 68px;
  height: 60px;
  background-color: #fff;
  animation: bubble 20s ease-in-out infinite;
  top: 22%;
  left: 92%;
}

.bubbles span:nth-child(6) {
  width: 20px;
  height: 20px;
  animation: bubble 4s ease-in-out infinite;
  top: 20%;
  left: 40%;
}

.bubbles span:nth-child(7) {
  width: 68px;
  height: 60px;
  background-color: #fff;
  animation: bubble 20s ease-in-out infinite;
  top: 9%;
  left: 92%;
}

.bubbles span:nth-child(8) {
  width: 25px;
  height: 25px;
  animation: bubble 5s ease-in-out infinite;
  top: 40%;
  left: 80%;
}

@keyframes bubble {
  0% {
    transform: translate(0);
  }

  50% {
    transform: translate(20px, -50px);
  }

  100% {
    transform: translate(0);
  }
}

