.footer--title1 {
  animation: move1 3s cubic-bezier(.27, .73, .58, .41) infinite alternate-reverse;
  position: relative;
}

@keyframes move1 {
  0% {
    color: pink;
    transform: scale(.8);
  }

  50% {
    color: #ffc1d1;
    transform: scale(1);
  }

  50.1% {
    color: #ffa5ba;
  }

  100% {
    color: #d9849e;
    transform: scale(.8);
  }
}

.footer--title2 {
  animation: move2 4s cubic-bezier(.27, .73, .58, .41) infinite alternate-reverse;
  position: relative;
}

@keyframes move2 {
  0% {
    color: #b80d57;
    transform: scale(.8);
  }

  50% {
    color: #d41443;
    transform: scale(1);
  }

  50.1% {
    color: #f0264f;
  }

  100% {
    color: #ff3f6f;
    transform: scale(.8);
  }
}

.footer-icons__item {
  transition: transform .5s;
  display: inline-block;
}

.footer-icons__item:hover svg {
  animation-name: rotate, rotate-stop;
  animation-duration: 1s, .5s;
  animation-timing-function: linear, linear;
  animation-iteration-count: infinite, 1;
  animation-direction: normal, normal;
  animation-play-state: running, running;
  animation-delay: 0s, 0s;
  animation-fill-mode: forwards;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate-stop {
  100% {
    transform: rotate(360deg);
  }
}

.footer-contact {
  opacity: 1;
  transition: opacity .3s ease-in-out;
}

.footer-contact:hover {
  animation: shake .5s;
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  25%, 75% {
    transform: translateX(-10px);
  }

  50% {
    transform: translateX(10px);
